import home from './home.js';
import write from './write.js';
import content from './content.js';
import edit from './edit.js';
import {datas,findContentByNum,findDataByNum,deleteData,changeData,AddData,dataCount} from './data.js'
import queryString from 'query-string'
import ApiService from './api.js';

const apiservice = new ApiService;

const container = document.getElementById('root')
function router(){
    const routePath = location.pathname.replace('/','');
    if (routePath == '') {
        apiservice.getDataset.then((res)=>{console.log(res)})
        home(datas)
    }
    else if (routePath == `content`){
        const query = queryString.parse(location.search)
        const contentFound = findContentByNum(query.num)
        const dataFound = findDataByNum(query.num)
        content({contentFound,dataFound,deleteData})
    }
    else if (routePath == 'edit'){ //새로 작성이랑 수정이랑 구분
        const query = queryString.parse(location.search)
        if (location.search.replace('?','') == 'new'){
            const today = new Date;
            const date = today.toLocaleDateString().replace(/. /gi,'').replace('.','')
            const data = {num:dataCount+1,title:'',author:'',date}
            const content = ''
            edit({content,data,AddData})
        }
        else{
            const contentFound = findContentByNum(query.num)
            const dataFound = findDataByNum(query.num)
            edit({content:contentFound,data:dataFound,changeData})
        }
    }
    else
     {
        container.innerHTML = 'route가 존재하지 않습니다'
     }

}

window.addEventListener('locationchange',router)

router();
