export default class ApiService{
    getDataset = async ()=>{
        return await customFecth(
            {method:"GET"},
            `/datas`)
    } 
    findDataByNum = async (num)=>{
        return await customFecth(
            {method:"GET"},
            `/datas?num=${num}`)
    }
    
    findContentByNum = async (num)=>{
        return await customFecth(
            {method:"GET"},
            `/content?num=${num}`)
    } 
    postContent = async (text,title,author,num,date)=>{
        return await customFecth(
            {method:"POST",body:{text,title,author,num,date}},
            `/content`)
    } 
    updateData = async (text,title,author,num,date)=>{
        return await customFecth(
            {method:"PUT",body:{text,title,author,num,date}},
            `/datas?num=${num}`)
    }
    deleteDataByNum = async (num)=>{
        return await customFecth(
            {method:"DELETE"},
            `/content?data=${num}`)
    } 

}

const customFecth = async (options,url) => {
    const res = await fetch(`http://localhost:8080${url}`, {
        ...options,
        headers: {...options.headers,"Content-Type":"application/json"},
    })
    return res
}